import React from 'react';
import ImageHandler from './imageHandler.jsx'
import './main.css';

class ImageSorterMain extends React.Component
{	
	constructor(props)
	{
		super(props);
	}

  render() 
  {
	  return(<div>		
				<div id="main">
					<p>main.jsx</p>
					<div id="leftSide">
						<p> Image Info </p>
						<p> Previous Images </p>
					</div>
					<div id="imageFrame">
						<ImageHandler />
					</div>
					<div id="rightSide">
						<p> Keep Folder </p>
						<p> Delete Folder </p>
						<button id="purgeButton">Purge Delete Folder</button>
					</div>
				</div>
		</div>);
  }
}

export default ImageSorterMain;